<template>
  <div class="tasks">
    <nav class="tasks--nav">
      <span
        :class="activePage == 0 ? activeLink : inActivLink"
        @click="PageNavigator(0)"
        >All</span
      >
      <span
        :class="activePage == 1 ? activeLink : inActivLink"
        @click="PageNavigator(1)"
        >By month</span
      >
      <span
        :class="activePage == 2 ? activeLink : inActivLink"
        @click="PageNavigator(2)"
        >By day</span
      >
    </nav>
    <div class="tasks--main">
      <!-- by month -->
      <div class="tasks--page tasks--page__month" v-if="activePage == 1">
        <h2 class="tasks--title">Select year and month</h2>
        <div class="tasks--container">
          <div class="tasks--year">
            <label class="tasks--label" for="year">Year</label>
            <select
              class="tasks--select"
              name="year"
              id="year"
              v-model="year"
              @change="LoadMonthTasks"
            >
              <option
                v-for="someYear in availableYears"
                :value="someYear"
                v-bind:key="someYear"
              >
                {{ someYear }}
              </option>
            </select>
          </div>
          <div class="tasks--month">
            <label class="tasks--label" for="month">Month</label>
            <select
              class="tasks--select"
              name="month"
              id="month"
              v-model="month"
              @change="LoadMonthTasks"
            >
              <option
                v-for="someMonth in availableMonths"
                :value="someMonth"
                v-bind:key="someMonth"
              >
                {{ someMonth }}
              </option>
            </select>
          </div>
        </div>
      </div>

      <!-- by day -->
      <div class="tasks--page tasks--page__day" v-if="activePage == 2">
        <h2 class="tasks--title">Select day</h2>
        <input
          class="tasks--select"
          type="date"
          v-model="dateDayTasks"
          @change="LoadDayTasks"
        />
      </div>

      <!-- task list -->
      <div class="tasks--content">
        <h2 class="tasks--title">Total: {{ amount }}</h2>
        <div
          class="tasks--task"
          v-for="(task, number) in tasksList"
          v-bind:key="task.id"
        >
          <h3 class="tasks--title">{{ task.title }}</h3>
          <div class="tasks--desc">{{ task.desc }}</div>
          <div class="tasks--container">
            <div class="tasks--time tasks--time__start">
              Start: {{ task.start.replace("T", " ") }}
            </div>
            <div class="tasks--time tasks--time__stop">
              Stop: {{ task.stop.replace("T", " ") }}
            </div>
          </div>
          <button
            class="tasks--button tasks--button__delete"
            @click="DeleteTask(task.id, number)"
          >
            x
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tasks",
  data: function () {
    return {
      // for nav buttons
      activePage: 0,
      activeLink: "tasks--link tasks--link__active",
      inActivLink: "tasks--link",

      // variables
      amount: 0,
      tasksList: [],
      currentDate: new Date(),
      availableYears: [],
      availableMonths: [],
      availableDays: [],
      year: null,
      month: null,
      day: null,
      dateDayTasks: "",
    };
  },
  methods: {
    LoadAllTasks: async function () {
      this.tasksList = [];

      let response = await fetch("/tasks", {
        method: "POST",
      });
      if (response.status != 200) {
        this.$parent.Exit(false);
        alert("Session expired");
      } else {
        this.tasksList = await response.json();
        this.TotalTime();
      }
    },
    LoadMonthTasks: async function () {
      this.tasksList = [];

      // create form data
      let formData = new FormData();
      let lastDay = new Date(this.year, this.month + 1, 0).getDate();

      formData.append("date_start", `${this.year}-${this.month}-1T00:00`);
      formData.append(
        "date_stop",
        `${this.year}-${this.month}-${lastDay}T23:59`
      );

      let response = await fetch("/date_tasks", {
        method: "POST",
        body: formData,
      });
      if (response.status != 200) {
        this.$parent.Exit(false);
        alert("Session expired");
      } else {
        this.tasksList = await response.json();
        this.TotalTime();
      }
    },
    LoadDayTasks: async function () {
      this.tasksList = [];

      // create form data
      let formData = new FormData();
      let parsed_date = new Date(this.dateDayTasks);
      formData.append(
        "date_start",
        `${parsed_date.getFullYear()}-${
          parsed_date.getMonth() + 1
        }-${parsed_date.getDate()}T00:00`
      );
      formData.append(
        "date_stop",
        `${parsed_date.getFullYear()}-${
          parsed_date.getMonth() + 1
        }-${parsed_date.getDate()}T23:59`
      );

      let response = await fetch("/date_tasks", {
        method: "POST",
        body: formData,
      });
      if (response.status != 200) {
        this.$parent.Exit(false);
        alert("Session expired");
      } else {
        this.tasksList = await response.json();
        this.TotalTime();
      }
    },
    DeleteTask: async function (id, number) {
      if (
        confirm(
          `You are really want to delete '${this.tasksList[number].title}' task?`
        )
      ) {
        // create form data
        let formData = new FormData();
        formData.append("task_id", id);

        // delete on server
        await fetch("/delete", {
          method: "DELETE",
          body: formData,
        });

        // update tasks
        await this.LoadAllTasks();

        // update amount
        this.TotalTime();
      }
    },

    TotalTime: function () {
      let minutes = 0;
      for (let i = 0; i < this.tasksList.length; i++) {
        const start = new Date(this.tasksList[i].start);
        const stop = new Date(this.tasksList[i].stop);
        const different = stop.getTime() - start.getTime();
        minutes += different > 0 ? different / 1000 / 60 : 0;
      }

      this.amount = `${Math.floor(minutes / 60)} hour(s) ${
        minutes - Math.floor(minutes / 60) * 60
      } minut(s)`;
    },
    PageNavigator: async function (pageNum) {
      // change page
      this.activePage = parseInt(pageNum);

      // Load tasks for page
      switch (pageNum) {
        // Load all tasks
        case 0:
          await this.LoadAllTasks();
          break;
        case 1:
          await this.LoadMonthTasks();
          break;
        case 2:
          await this.LoadDayTasks();
          break;
      }

      // Update amount time
      await this.TotalTime();
    },
    CreateArray: function (start, stop) {
      let arr = [];

      for (let number = start; number <= stop; number++) {
        arr.push(number);
      }

      return arr;
    },
  },
  mounted: async function () {
    if (this.$parent.$parent.isAuth) {
      // init dates
      this.year = this.currentDate.getFullYear();
      this.month = this.currentDate.getMonth() + 1;
      this.day = this.currentDate.getDate();
      this.availableYears = this.CreateArray(this.year - 1, this.year + 1);
      this.availableMonths = this.CreateArray(1, 12);
      (this.availableDays = this.CreateArray(
        1,
        new Date(this.year, this.month + 1, 0).getDate()
      )),
        (this.dateDayTasks = `${new Date().getFullYear()}-${
          new Date().getMonth() + 1
        }-${new Date().getDate()}`),
        // load all tasks
        await this.LoadAllTasks();
      this.TotalTime();
    }
  },
};
</script>

