<template>
  <div id="app">
    <div v-if="isAuth">
      <UserMenu></UserMenu>
    </div>
    <div v-else>
      <nav class="root--nav">
        <span :class="activePage == 0 ? activeLink : inActivLink" @click="PageNavigator(0)">Login</span>
        <span :class="activePage == 1 ? activeLink : inActivLink" @click="PageNavigator(1)">Register</span>
      </nav>
      <div class="root--container">
        <LoginForm v-if="activePage == 0"></LoginForm>
        <RegisterForm v-if="activePage == 1"></RegisterForm>
      </div>
    </div>
  </div>
</template>

<script>
import LoginForm from "./components/Login.vue";
import RegisterForm from "./components/Register.vue";
import UserMenu from "./components/UserMenu.vue";

export default {
  name: "Root",
  data: function () {
    return {
      activePage: 0,
      activeLink: "root--link root--link__active",
      inActivLink: "root--link",
      isAuth: false,
    };
  },
  components: {
    LoginForm,
    RegisterForm,
    UserMenu,
  },
  methods: {
    CheckStatus: async function () {
      await fetch("is_auth")
        .then((response) => response.json())
        .then((data) => (this.isAuth = data.status));
    },

    PageNavigator: function (pageName) {
      this.activePage = parseInt(pageName);
    },
  },
  mounted: function () {
    this.CheckStatus();
  },
};
</script>

<style lang="scss">
.root {
  &--nav {
    display: flex;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
  }
  &--link {
    padding: 10px 20px;
    cursor: pointer;
    user-select: none;

    &__active {
      background-color: lightgray;
    }
  }
  &--container {
    height: 100%;
  }
}
</style>
