<template>
  <div class="user-menu">
    <nav class="nav-user-menu">
      <span :class="activePage == 0 ? activeLink : inActivLink" @click="PageNavigator(0)">Tasks</span>
      <span :class="activePage == 1 ? activeLink : inActivLink" @click="PageNavigator(1)">Create task</span>
      <span class="nav-user-menu--link nav-user-menu--link__exit" @click="Exit">Exit</span>
    </nav>
    <div class="content">
      <Tasks v-if="activePage == 0"></Tasks>
      <CreateForm v-if="activePage == 1"></CreateForm>
    </div>
  </div>
</template>

<script>
// import LoginForm from "./components/Login.vue";
import Tasks from "./Tasks.vue";
import CreateForm from "./Create.vue";

export default {
  name: "UserMenu",
  data: function () {
    return {
      activePage: 0,
      activeLink: "nav-user-menu--link nav-user-menu--link__active",
      inActivLink: "nav-user-menu--link",
    };
  },
  components: {
    Tasks,
    CreateForm,
  },
  methods: {
    PageNavigator: function (pageNum) {
      this.activePage = parseInt(pageNum);
    },
    Exit: function (askUser = true) {
      let userAnswer = askUser ? confirm("You are really want to exit?") : false;
      if (userAnswer || !askUser) {
        // delete all cookies
        document.cookie = "token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;"

        // show login page
        this.$parent.isAuth = false;
      }
    }
  },
  mounted: function () {},
};
</script>

<style lang="scss">
.user-menu {
  .nav-user-menu {
    display: flex;
    justify-content: center;

    &--link {
      padding: 10px 20px;
      cursor: pointer;
      user-select: none;
      text-align: center;

      &__active {
        background-color: lightgray;
      }

      &__exit {
        background-color: lightcoral;
      }
    }
  }
}
</style>
