<template>
  <div class="register">
    <h2 class="register--title">Registration</h2>
    <div class="register--container">
      <input
        class="register--input"
        type="text"
        v-model="name"
        placeholder="Your name"
      />
      <input
        class="register--input"
        type="text"
        v-model="surname"
        placeholder="Your surname"
      />
      <input
        class="register--input"
        type="text"
        v-model="login"
        placeholder="Your login"
      />
      <input
        class="register--input"
        type="password"
        v-model="password"
        placeholder="Your password"
      />
      <input
        class="register--input"
        type="password"
        v-model="password_confirm"
        placeholder="Your password"
      />
      <button class="register--button" @click="CreateUser">Create</button>
    </div>
    <div class="register--status">{{ statusMessage }}</div>
  </div>
</template>

<script>
export default {
  name: "RegisterForm",
  data: function () {
    return {
      statusMessage: "",
      name: "",
      surname: "",
      login: "",
      password: "",
      password_confirm: "",
    };
  },
  methods: {
    CreateUser: async function () {
      if (
        this.name == "" ||
        this.login == "" ||
        this.surname == "" ||
        this.password == "" ||
        this.password_confirm == ""
      ) {
        this.statusMessage = "Check inputed data";
        return;
      } else if (this.password != this.password_confirm) {
        this.statusMessage = "Passwords not same";
        return;
      }
      // create form data
      let formData = new FormData();
      formData.append("name", this.name);
      formData.append("surname", this.surname);
      formData.append("login", this.login);
      formData.append("password", this.password);
      formData.append("password_confirm", this.password_confirm);

      // override values
      this.name = "";
      this.surname = "";
      this.login = "";
      this.password = "";
      this.password_confirm = "";

      // create task on server
      await fetch("/register", {
        method: "POST",
        body: formData,
      })
        .then((response) => response.json())
        .then((data) => (this.statusMessage = data.detail ?? data.message));
    },
  },
};
</script>

