<template>
  <div class="login" @keyup.enter="LogIn">
    <h3 class="login--title">Auth form</h3>
    <div class="login--container">
      <input
        class="login--input"
        type="text"
        placeholder="Your login"
        v-model="login"
      />
      <input
        class="login--input"
        type="password"
        placeholder="Your password"
        v-model="password"
      />
      <button class="login--button" @click="LogIn">LogIn</button>
    </div>
    <div class="login--status">{{ statusMessage }}</div>
  </div>
</template>

<script>
export default {
  name: "LoginForm",
  data: function () {
    return {
      login: "",
      password: "",
      statusMessage: "",
    };
  },
  methods: {
    LogIn: async function () {
      // check for null data
      if (this.login.trim() == "" || this.password.trim() == "") {
        this.statusMessage = "Login or password can not be empty";
        return;
      }
      // login request
      // create form data
      let formData = new FormData();
      formData.append("login", this.login);
      formData.append("password", this.password);

      // delete user users
      this.userLogin = "";
      this.userPassword = "";

      // request to server
      await fetch("login", {
        method: "POST",
        body: formData,
      })
        .then((res) => res.json())
        .then((data) => (this.statusMessage = data.detail ?? data.message));

      // change status
      await this.$parent.CheckStatus();
    },
  },
};
</script>

