<template>
  <div class="create">
    <h2 class="create--title">Create task</h2>
    <div class="create--container">
      <input class="create--input" type="text" v-model="taskTitle" placeholder="Title" />
      <input class="create--input" type="text" v-model="taskDesc" placeholder="Description" />
      <input class="create--input"
        type="datetime-local"
        v-model="taskStart"
        @change="UpdateStopTime"
      />
      <input class="create--input" type="datetime-local" v-model="taskStop" />
      <button class="create--button" @click="CreateTask">Create</button>
    </div>
    <div class="create--status">{{ statusMessage }}</div>
  </div>
</template>

<script>
export default {
  name: "CreateForm",
  data: function () {
    return {
      statusMessage: "",
      taskTitle: "",
      taskDesc: "",
      taskStart: "",
      taskStop: "",
      password_confirm: "",
    };
  },
  methods: {
    CreateTask: async function () {
      if (
        this.taskTitle == "" ||
        this.taskDesc == "" ||
        this.taskStart == "" ||
        this.taskStop == ""
      ) {
        this.statusMessage = "Check inputed data";
        return;

        // Check for task time (minimum 5 min)
      } else if (
        new Date(this.taskStart).getTime() >=
        new Date(this.taskStop).getTime() - 299000
      ) {
        this.statusMessage = "Check start stop time. Task time minimum 5 min";
        return;
      }
      // create form data
      let formData = new FormData();
      formData.append("title", this.taskTitle);
      formData.append("description", this.taskDesc);
      formData.append("start", this.taskStart);
      formData.append("stop", this.taskStop);

      // override values
      this.taskTitle = "";
      this.taskDesc = "";

      // create task on server
      await fetch("/create", {
        method: "POST",
        body: formData,
      })
        .then((response) => response.json())
        .then((data) => (this.statusMessage = data.detail ?? data.message));
    },
    UpdateStopTime: function () {
      this.taskStop = this.taskStart;
    },
  },
};
</script>

